import template from './multi-steps.html';
import templateNested from './nested-multi-steps.html';

function MultiStepsDirective() {
  'ngInject';

  return {
    restrict: 'E',
    scope: {
      currentIndex: '=',
      nested: '@',
      canNotClick: '@',
    },
    template: function(el,attrs) {
      if (attrs.nested) {
        return templateNested;
      } else {
        return template;
      }
    },
    controller: MultiStepsController,
    controllerAs: 'vm',
    bindToController: true,
    transclude: true,
  };
}

class MultiStepsController {
  constructor($scope) {
    'ngInject';

    this.steps = [];
    this.currentIndex = 1;
    this.nested = $scope.vm.nested;
    this.canNotClick = $scope.vm.canNotClick;
    this.$scope = $scope;
    this.activate();
  }

  activate() {
    this.$scope.$watch('vm.currentIndex', (newValue, oldValue) => {
      if (typeof(newValue) !== 'number' || newValue > this.steps.length || newValue < 1) {
        this.currentIndex = 1;
        return;
      }
      if (this.steps[newValue - 1].noActive) {
        if (newValue > oldValue) {
          this.currentIndex++;
        } else {
          this.currentIndex--;
        }
      } else if (this.steps[newValue - 1].isLocked) {
        this.currentIndex = oldValue;
      } else {
        let currentStep = this.steps[newValue - 1];
        this.activeStep(currentStep);
      }
    });
  }

  activeStep(step) {
    if (step.noActive || step.isLocked) return;
    this.steps.forEach(stepScope => {
      if (stepScope !== step) {
        stepScope.isActive = false;
      }
    });
    step.isActive = true;
    this.currentIndex = this.steps.indexOf(step) + 1;
  }

  addStep(stepScope) {
    if (typeof(stepScope.index) !== 'number') {
      this.steps.push(stepScope);
    } else {
      this.steps.splice(stepScope.index, 0, stepScope);
    }

    stepScope.index = this.steps.indexOf(stepScope) + 1;
    if (stepScope.index === 1) {
      stepScope.isActive = true;
    }

    stepScope.$on('$destroy', event => {
      this.removeStep(stepScope);
    });

  }

  removeStep(step) {
    var index = this.steps.indexOf(step);
    if (index !== -1) {
      this.steps.splice(index, 1);
    }
  }
}

export default MultiStepsDirective;



