import * as dialog from './dialog.js';
import './dao-dialog.scss';

angular.module('dao.dialog', ['dao.multiStep'])
  .factory('$daoDialogStack', dialog.DaoDialogStack)
  .provider('$daoDialog', dialog.DaoDialogProvider)
  .directive('daoDialogBackdrop', dialog.daoDialogBackdrop)
  .directive('daoDialogWindow', dialog.daoDialogWindow)
  .directive('daoDialogAnimationClass', dialog.daoDialogAnimationClass)
  .directive('daoDialogTransclude', dialog.daoDialogTransclude);

import DaoDialogHeader from './dao-dialog-header.js';
import DaoMultiStep from './dao-multi-step.js';
import DaoStep from './dao-step.js';

angular.module('dao.multiStep', [])
  .directive('daoDialogHeader', DaoDialogHeader)
  .directive('daoMultiStep', DaoMultiStep)
  .directive('daoStep', DaoStep);

export default 'dao.dialog';



