import './dao-tab.scss';
import tabHTML from './dao-tab.html'
import tabsetHTML from './dao-tabset.html'
import * as daotab from './dao-tab.js';

const moduleName = 'dao.tab';

angular.module(moduleName, [])
  .controller('DaoTabsetController', ['$scope', daotab.DaoTabsetController])

  .directive('daoTabset', daotab.daoTabset)

  .directive('daoTab', ['$parse', daotab.daoTab])

  .directive('daoTabHeadingTransclude', daotab.daoTabHeadingTransclude)

  .directive('daoTabContentTransclude', daotab.daoTabContentTransclude);

export default moduleName;



