import template from './step.html';

function StepDirective($timeout) {
  'ngInject';

  return {
    restrict: 'E',
    require: '^daoPanelMultiSteps',
    scope: {
      index: '@',
      title: '@',
      isSuccessful: '=',
      isWarning: '=',
      isLocked: '=',
      isMainmenu: '@',
      isSubmenu: '@',
      noActive: '@',
    },
    template: template,
    replace: true,
    transclude: true,
    link: StepLink,
  };

  function StepLink(scope, element, attrs, daoMultiStepCtrl) {

    function getIndex(current, obj) {
      for (let i = 0; i < obj.length; i++) {
        if (obj[i] == current) {
          return i;
        }
      }
    }

    const sibling = element[0].parentElement.children;
    const index = getIndex(element[0], sibling);
    scope.index = index;
    daoMultiStepCtrl.addStep(scope);

    scope.$watch('isActive', function(value) {
      element.toggleClass('active', !!value);
    });
  }
}

export default StepDirective;



