const moduleName = 'dao.editableTable';

import editableTableDirective from './editable-table';
import daoFixedTableDirective from './dao-fixed-table';

angular.module(moduleName, [])
  .directive('daoFixedTable', daoFixedTableDirective)
  .directive('editableTable', editableTableDirective);

export default moduleName;



