import template from './dao-input.html';
import './dao-input.scss';

function DaoInputDirective($compile) {
  'ngInject';

  return {
    priority: 3,
    restrict: 'A',
    template: template,
    replace: true,
    transclude: 'element',
    compile: DaoInputCompile,

    //link: DaoInputLink,
    //controller: DaoInputController,
    //controllerAs: 'vm',
    //bindToController: true,
  };

  function DaoInputCompile(el, attrs) {
    return DaoInputLink;
  }

  function DaoInputLink(scope, el, attrs, ctrl, $transclude) {
    $transclude(scope, clone => {
      var input = el.find('form-area');
      input.replaceWith(clone);
    });
  }
}

export default DaoInputDirective;



