import template from './dao-multi-step.html';

const forEach = angular.forEach;

/*@ngInject*/
function DaoMultiStep() {
  return {
    restrict: 'EA',
    controller: DaoMultiStepController,
    controllerAs: 'multi',
    scope: {
      preStep: '=',
      nextStep: '='
    },
    template: template,
    bindToController: true,
    transclude: true,
  };
}


/*@ngInject*/
class DaoMultiStepController {

  constructor($scope) {

    this.steps = [];
    this.currentIndex = 0;

    $scope.$watch('multi.currentIndex', (value) => {
      this.activeStep();
    });

    this.preStep = (step_length = 1) => {
      this.currentIndex = this.currentIndex - step_length;
    }

    this.nextStep = (step_length = 1) => {
      this.currentIndex = this.currentIndex + step_length;
    }

  }

  activeStep() {
    let currentStep = this.steps[this.currentIndex];

    if (this.steps.length == 0) return;

    forEach(this.steps, function(step) {
      if (step !== currentStep) {
        step.isActive = false;
      }
    });

    currentStep.isActive = true;
  }

  addStep(stepScope) {
    this.steps.push(stepScope);

    this.activeStep();

    stepScope.$on('$destroy', event => {
      this.removeStep(stepScope);
    });
  }

  removeStep(step) {
    var index = this.steps.indexOf(step);
    if (index !== -1) {
      this.steps.splice(index, 1);
    }
  }

}

export default DaoMultiStep;



