import template from './dao-switch.html';

function SwitchDirective() {

  function linkFun(scope, el, attrs) {
    scope.attrs = attrs;
    if (!attrs.disabled) {
      scope.click = () => {
        scope.ngModel = !scope.ngModel;
      }
    }
    if (scope.ngChange) {
      scope.$watch('ngModel', function(value) {
        scope.ngChange();
      })
    }
  }

  return {
    restrict: 'AE',
    replace: true,
    scope: {
      ngModel: '=',
      ngChange: '&'
    },
    template: template,
    link: linkFun
  }
}

export default SwitchDirective;



