import template from './editable-table.html';

function editableTableDirective() {
  return {
    restrict: 'E',
    scope: {
      data: '=',
    },
    template: template,
    replace: true,
    controller: editableTableController,
    controllerAs: 'vm',
  }
}

class editableTableController {
  constructor($scope, $element, $attrs) {
    'ngInject';

    this.data = $scope.data;
    this.data_clone = angular.copy(this.data, {});
  }

  edit(rowIndex) {
    if (!this.checkError()) return;
    this.editIndex = rowIndex;
  }

  /**
   * This method is called
   * with composite ng-options:
   * [{
   *   label: '',
   *   value: ''
   * }]
   */
  updateCell(td) {
    for (let i = 0; i < td.options.length; i++) {
      if (td.options[i].value == td.value) {
        td.label = td.options[i].label;
      }
    }
  }

  switch(rowIndex, colIndex) {
    let status = !!this.data_clone.body[rowIndex][colIndex].status;
    this.data_clone.body[rowIndex][colIndex].status = !status;
  }

  save() {
    if (!this.checkError()) return;
    angular.copy(this.data_clone, this.data);
    this.editIndex = null;
  }

  delete(rowIndex) {
    this.data_clone.body.splice(rowIndex, 1);
    angular.copy(this.data_clone, this.data);
  }

  add() {
    if (!this.checkError()) return;
    angular.merge(this.data_clone, this.data);
    if (this.data_clone.body.length > this.data.body.length) {
      angular.copy(this.data_clone, this.data);
    };
    const rowNum = this.data_clone.body.length;
    this.data_clone.body.push(angular.copy(this.data_clone.addRow.body, []));
    this.editIndex = rowNum;
  }

  checkError() {
    if (typeof(this.editIndex) !== 'number') return true;

    if (this.data_clone.checkFun) {
      const errorMessage = this.data_clone.checkFun(this.data_clone.body);
      if (errorMessage) {
        this.data_clone.errorMessage = errorMessage;
        return false;
      }
    };

    let correct = true;
    this.data_clone.errorMessage = '';
    angular.forEach(this.data_clone.body[this.editIndex], v => {
      v.error = false;
      let value;
      v.value && (value = v.value.toString().trim());
      if (v.editable && !v.type && !v.options) {
        if (!value && v.required) {
          correct = false;
          v.error = true;
          this.data_clone.errorMessage = '';
          return;
        }
        if (v.required && v.reg && !v.reg.test(v.value)) {
          correct = false;
          v.error = true;
          this.data_clone.errorMessage = '';
          return;
        }
      }
    });
    return correct;
  }
}

export default editableTableDirective;



