import DaoDropdown from './dao-dropdown/index';
import DaoFeedback from './dao-feedback/index';
import DaoSwitch from './dao-switch/index';
import DaoRadioBlock from './dao-radio/index';
import DaoTab from './dao-tab/index';
import DaoEditableTable from './dao-table/index';
import DaoSelectAll from './dao-select-all/index';
import DaoTopNav from './dao-nav/index';
import DaoClipboard from './dao-clipboard/index';

var app = angular.module('daoStyle', [
  DaoInput,
  DaoDialog,
  DaoTooltip,
  DaoPopover,
  DaoPanelMultiStep,
  DaoSwitch,
  DaoDropdown,
  DaoFeedback,
  DaoRadioBlock,
  DaoTab,
  DaoEditableTable,
  DaoSelectAll,
  DaoTopNav,
  DaoClipboard,
]);

import DaoDemoBlockDirective from './dao-demo/dao-demo.directive.js';
app.directive('daoDemoBlock', DaoDemoBlockDirective)

export default 'daoStyle';



