import daoInput  from './dao-input.directive.js';
import daoInputState  from './dao-input-state.directive.js';
import editableInput from './editable-dao-input.directive.js';

angular
  .module('dao.input', [])
  .directive('daoInputState', daoInputState)
  .directive('daoInput', daoInput)
  .directive('editableDaoInput', editableInput);

export default 'dao.input';



