function DaoInputStateDirective($compile) {
  'ngInject';

  return {
    priority: 4,
    restrict: 'A',
    scope: {
      state: '=daoInputState',
    },

    //compile: DaoInputCompile,
    link: Link,

    //controller: DaoInputController,
    //controllerAs: 'vm',
    //bindToController: true,
  };

  function Link(scope, el, attrs) {
    scope.$watch('state', (_new, _old) => {
      attrs.$removeClass(_old);
      attrs.$addClass(_new);
    });
  }

}

export default DaoInputStateDirective;



