import template from './dao-clipboard.html';
import './dao-clipboard.scss';
import 'ngclipboard/dist/ngclipboard.min';

class DaoClipboardController {
  constructor() {
    'ngInject';
    this.copied = false;
  }
}

function DaoClipboard() {
  'ngInject';

  return {
    restrict: 'E',
    scope: {
      content: '=',
    },
    template: template,
    transclude: true,
    controller: DaoClipboardController,
    controllerAs: 'vm',
    bindToController:true,
  };
}

angular.module('dao.clipboard', ['ngclipboard'])
  .directive('daoClipboard', DaoClipboard);

export default 'dao.clipboard';



