import template from './dao-demo.html';
import './dao-demo.scss';

function DaoDemoBlockDirective() {
  'ngInject';

  return {
    restrict: 'E',
    scope: {
      title: '@',
    },
    template: template,
    transclude: true,
    controller: function() {},

    controllerAs: 'dao',
    bindToController:true,
  };
}

export default DaoDemoBlockDirective;




