import * as position from './dao-position.js';
import * as stackedMap from './dao-stackedMap.js';

angular.module('dao.position', [])
  .factory('$daoPosition', position.$daoPosition);

angular.module('dao.stackedMap', [])
  .factory('$daoStackedMap', stackedMap.$daoStackedMap);

import * as tooltip from './dao-tooltip.js';
import './tooltip.scss';

const moduleName = 'dao.tooltip';
angular.module(moduleName, ['dao.position', 'dao.stackedMap'])
  .provider('$daoTooltip', tooltip.$daoTooltip)
  .directive('daoTooltipTemplateTransclude', tooltip.daoTooltipTemplateTransclude)
  .directive('daoTooltipClasses', tooltip.daoTooltipClasses)
  .directive('daoTooltipPopup', tooltip.daoTooltipPopup)
  .directive('daoTooltip', tooltip.daoTooltip)
  .directive('daoTooltipTemplatePopup', tooltip.daoTooltipTemplatePopup)
  .directive('daoTooltipTemplate', tooltip.daoTooltipTemplate)
  .directive('daoTooltipHtmlPopup', tooltip.daoTooltipHtmlPopup)
  .directive('daoTooltipHtml', tooltip.daoTooltipHtml);

export default moduleName;



