import './multi-step.scss';

const moduleName = 'dao.panelMultiStep';

import MultiStepsDirective from './multi-steps';
import MultiStepDirective from './step';

angular.module(moduleName, [])
  .directive('daoPanelMultiSteps', MultiStepsDirective)
  .directive('daoPanelStep', MultiStepDirective);

export default moduleName;



