const moduleName = 'dao.radioBlock';

import RadioGroupDirective from './radio-group';
import RadioBlockDirective from './radio-block';
import RadioGridDirective from './radio-grid';

angular.module(moduleName, [])
  .directive('daoRadioGroup', RadioGroupDirective)
  .directive('daoRadioBlock', RadioBlockDirective)
  .directive('daoRadioGrid', RadioGridDirective);

export default moduleName;



