/**
 * The following features are still outstanding: popup delay, animation as a
 * function, placement as a function, inside, support for more triggers than
 * just mouse enter/leave, and selector delegatation.
 */
import * as popover from './dao-popover.js';
import './popover.scss';

const moduleName = 'dao.popover';
angular.module(moduleName, ['dao.tooltip'])
  .directive('daoPopoverTemplatePopup', popover.daoPopoverTemplatePopup)
  .directive('daoPopoverTemplate', popover.daoPopoverTemplate)
  .directive('daoPopoverHtmlPopup', popover.daoPopoverHtmlPopup)
  .directive('daoPopoverHtml', popover.daoPopoverHtml)
  .directive('daoPopoverPopup', popover.daoPopoverPopup)
  .directive('daoPopover', popover.daoPopover);

export default moduleName;



