import template from './radio-group.html';

function RadioGroupDirective() {
  return {
    restrict: 'E',
    scope: {
      name: '@',
      defaultChecked: '=',
      checkedItem: '=',
    },
    template: template,
    replace: true,
    controller: RadioGroupController,
    controllerAs: 'vm',
    transclude: true,
  }
}

class RadioGroupController {
  constructor($scope, $element, $attrs) {
    'ngInject';

    this.$scope = $scope;
    this.$element = $element
    this.$attrs = $attrs;
    this.name = $scope.name;

    if ($scope.defaultChecked && $scope.defaultChecked >= 1) {
      this.defaultChecked = parseInt($scope.defaultChecked);
    } else {
      this.defaultChecked = 1;
    };

    this.radios = [];
  }

  addRadioItem(radioScope) {
    this.radios.push(radioScope);

    const index = this.radios.indexOf(radioScope);
    if (index === this.defaultChecked - 1) {
      this.check(radioScope);
    }
  }

  check(radioScope) {
    angular.forEach(this.radios, v => {
      if (v !== radioScope) {
        v.checked = false;
      }
    });
    radioScope.checked = true;
    this.$scope.checkedItem = {
      id: radioScope.id,
      name: radioScope.name,
      value: radioScope.value,
    };
  }

  setWidth(element) {
    const gridNum = this.$scope.$eval(this.$attrs.gridNum);

    if (!gridNum || gridNum < 1) return;

    element.css({'width': 100 / parseInt(gridNum) + '%'});
  }
}

export default RadioGroupDirective;



