import popoverTpl from './popover.html';
import popoverTemplateTpl from './popover-template.html';
import popoverHtmlTpl from './popover-html.html';

'use strict';

export /*@ngInject*/ function daoPopoverTemplatePopup() {
  return {
    replace: true,
    scope: { title: '@', contentExp: '&', placement: '@', popupClass: '@', animation: '&', isOpen: '&',
      originScope: '&', templateType: '@', },
    template: popoverTemplateTpl,
  };
}

export /*@ngInject*/ function daoPopoverTemplate($daoTooltip) {
  return $daoTooltip('daoPopoverTemplate', 'popover', 'click', {
    useContentExp: true,
  });
}

export /*@ngInject*/ function daoPopoverHtmlPopup() {
  return {
    replace: true,
    scope: { contentExp: '&', title: '@', placement: '@', popupClass: '@', animation: '&', isOpen: '&' },
    template: popoverHtmlTpl,
  };
}

export /*@ngInject*/ function daoPopoverHtml($daoTooltip) {
  return $daoTooltip('daoPopoverHtml', 'popover', 'click', {
    useContentExp: true,
  });
}

export /*@ngInject*/ function daoPopoverPopup() {
  return {
    replace: true,
    scope: { title: '@', content: '@', placement: '@', popupClass: '@', animation: '&', isOpen: '&' },
    template: popoverTpl,
  };
}

export /*@ngInject*/ function daoPopover($daoTooltip) {
  return $daoTooltip('daoPopover', 'popover', 'click');
};



