import './dao-top-nav.scss';
import template from './dao-top-nav.html';

function DaoTopNavDirective($daoPosition, $compile, $timeout, $interval) {
  'ngInject';

  function linkFun(scope, element, attrs) {
    $timeout(() => {
      const navItemList = angular.element(element[0].querySelectorAll('.dao-nav-item'));
      const pseudo = angular.element(element[0].querySelector('.dao-pseudo-hover-state'));
      let activeNavItem = element[0].querySelector('.dao-nav-item.active');

      // pseudo  active item 
      function findActiveItem() {
        const interval = $interval(() => {
          activeNavItem = element[0].querySelector('.dao-nav-item.active');
          if (activeNavItem) {
            $interval.cancel(interval);
            activeNavItem = angular.element(activeNavItem);
            activeNavItem.triggerHandler('mouseenter');
          }
        }, 100);
      }

      findActiveItem();

      navItemList.bind('mouseenter', event => {
        const target = event.target;
        const position = $daoPosition.position(target);
        pseudo.css('left', `${position.left}px`);
        pseudo.css('width', `${position.width}px`);
        // pseudo.addClass('active');
      });
      navItemList.bind('mouseleave', event => {
        // pseudo.removeClass('active');
        const toElement = angular.element(event.toElement);
        if (!toElement.hasClass('dao-nav-item')) {
          findActiveItem();
        }
      });

    });
  }

  return {
    restrict: 'E',
    template: template,
    transclude: true,
    link: linkFun,
  };
}

export default DaoTopNavDirective;




